import * as React from 'react';
import { View } from '../common/elements';
import BlockSpinner from '../common/block-spinner';
import { ITickets } from '../../models/store/tickets';
import { ITicket } from '../../models/home';
import { ROLE } from '../../constants';
import { isNN } from '../../util';

interface IProps {
  data: ITickets,
  role: string
};

export default (props: IProps) => {
  
  const getBatchColor = (status: string, createdAt: Date) => {
    let date = new Date();
    createdAt = new Date(createdAt);
    switch(status.toUpperCase()){
      case "INIT":
      return (createdAt.getFullYear() < date.getFullYear() || createdAt.getMonth() !== date.getMonth() || date.getDate() - createdAt.getDate() > 2) ? "badge-danger" : "badge-warning";
      case "IN-PROGRESS":
      return "badge-info";
      case "RESOLVED":
      return "badge-success";
      default:
      return "";
    }
  }

  return (
    <View className="masonry-item col-md-6">
      <View className="bd bgc-white p-20">
        <View className="layers">
          <View className="layer w-100 mB-10">
            <h6 className="lh-1">Tickets</h6>
          </View>
          <View className="layer w-100">
            <ul className="list-task list-group" data-role="tasklist">
            {props.data.isUpToDate ?
            (!props.data.isError && isNN(props.data.data)) ?
            props.data.data.map((d: ITicket, i: number) => {
              return(
                <li className="list-group-item bdw-0" data-role="task">
                <View className="peers ai-c">
                  <label className=" peers peer-greed js-sb ai-c">
                    <span className="peer peer-greed">{(props.role !== ROLE.USER ? d._homeId + ': ' : '') +  d.title}</span>
                    <span className="peer">
                      <span className={"badge badge-pill fl-r lh-0 p-10 " + getBatchColor(d.status, d.createdAt)}>{d.status}</span>
                    </span>
                  </label>
                </View>
              </li>
              );
            })
            :
            <li className="list-group-item bdw-0" data-role="task">
                <View className="peers ai-c">
                  <label className=" peers peer-greed js-sb ai-c">
                    <span className="peer peer-greed">No ticket found !!!</span>
                  </label>
                </View>
              </li>
            :
            <BlockSpinner visible={true} />
            }
            </ul>
          </View>
        </View>
      </View>
    </View>
  );
};