import React, {useState, useEffect} from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { View, TextInput, Image, Text } from '../../common/elements';
import Button from '../../common/button';
import Spinner from '../../common/spinner';
import Breadcrum from '../../common/breadcrum';
import { IAppUser } from '../../../models/app-user';
import { IServant, ITakeaway } from '../../../models/home';
import Toast from  '../../../util/toast';
import { isNN } from '../../../util';
import { FILE_BASE_PATH, ROLE } from '../../../constants';
import { toDateTimeString } from '../../../util/convert';

interface IProps {
  appUser: IAppUser,
  gateNo: string,
  onLoad: (sCallback: (result: Array<IServant>) => void, fCallback: (message: string) => void) => void,
  onClocking: (id: string, status: string, gateNo: string, callback: (result: string) => void) => void,
  onLoadClockings: (id: string, pn: number, rc: number, callback: (result: Array<any>) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [data, setData] = useState([] as Array<IServant>);
  const [searchText, setSearchText] = useState('');
  const [viewId, setViewId] = useState('');
  const [takeaways, setTakeaways] = useState([] as Array<ITakeaway>);
  const [clocking, setClocking] = useState({
    viewId: '',
    clockings: [] as Array<any>,
    clockingPN: 1,
    clockingRC: 10  
  });

  const loadData = () => {
    setIsLoading(true);
    props.onLoad((result: Array<IServant>) => {
      setIsLoading(false);
      setData(result);
    }, (message: string) => {
      setIsLoading(false);
      Toast.fail(message);
    });
  };

  useEffect(() => {
    loadData();
  }, []);

  const onClocking = (id: string, status: string) => {
    confirmAlert({
      title: (status === 'IN' ? 'Confirm to Allow Servant Entry' : 'Confirm to Exit Servant'),
      message: 'Are you sure you wish to ' + (status === 'IN' ? 'Allow the servant to Enter?' : 'exit servant?'),
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            setIsLoading(true);
            props.onClocking(id, status, props.gateNo, (result: string) => {
              if(isNN(result)){
                let tData = data;
                let foundIndex = tData.findIndex(v => v._id === id);
                tData[foundIndex].lastClocking = {
                  _id: id,
                  at: new Date(),
                  status: status
                };
                setData(tData);
                setIsLoading(false);
                Toast.success(status === 'IN' ? 'servant entered successfully.' : 'Servant exit successfully.');
              }else{
                setIsLoading(false);
                Toast.fail(status === 'IN' ? 'Failed to allow the entry of servant.' : 'Failed to exit servant.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  const onView = (id: string, takeaways: Array<ITakeaway>) => {
    if(viewId === id){
      setViewId('');
      setTakeaways([]);
      setClocking({
        ...clocking,
        viewId: '',
        clockings: [],
        clockingPN: 1
      });
    }else{
      setViewId(id);
      setTakeaways(takeaways);
      setClocking({
        ...clocking,
        viewId: '',
        clockings: [],
        clockingPN: 1
      });
    }
  };

  const onViewClocking = (id: string) => {
    if(clocking.viewId === id){
      setViewId('');
      setTakeaways([]);
      setClocking({
        ...clocking,
        viewId: '',
        clockings: [],
        clockingPN: 1
      });
    }else{
      setIsLoading(true);
      props.onLoadClockings(id, clocking.clockingPN, clocking.clockingRC, (result: Array<any>) => {
        setIsLoading(false);
        setViewId('');
        setTakeaways([]);
        setClocking({
          ...clocking,
          viewId: id,
          clockings: result
        });
      });
    }
  };

  const onViewMoreClockings = (id: string) => {
    setIsLoading(true);
    props.onLoadClockings(id, clocking.clockingPN + 1, clocking.clockingRC, (result: Array<any>) => {
      setIsLoading(false);
      let clockings = clocking.clockings;
      clockings.push(...result);
      setClocking({
        ...clocking,
        clockings: clockings,
        clockingPN: clocking.clockingPN + 1
      });
    });
  };

  let dData = data;
  if(isNN(searchText))
  {
    dData = data.filter(d => d.name.toLowerCase().indexOf(searchText.toLowerCase()) >= 0)
  }
  return(
    <View id='mainContent'>
      <View className="row gap-20 masonry pos-r">
        <View className="masonry-item col-12">
          <Breadcrum data={[{title: 'Servants', link: 'all-servants'}]} />
          <View className='bgc-white p-20'>
            <View className='mT-10 mB-5'>
              <TextInput type='text' placeholder="Enter search text" className='form-control' value={searchText} onChange={(event: any) => setSearchText(event.target.value)} />
            </View>
            <View className='p-10 mB-10'>
              {isNN(data) ?
              dData.map((d: IServant, i: number) => {
                return(
                  <View key={i} className='card m-1 d-ib'>
                    {isNN(d.image) &&
                    <View className='ta-c mB-5'>
                      <Image src={FILE_BASE_PATH + d.image} className='cur-p m-0' style={{height: '25vh', borderRadius: '50%'}} onClick={() => {
                        // setIsZoomImage(true);
                        // setZoomImage(d.image);
                        }} />
                    </View>
                    }
                    <View className='container'>
                    <View className='mT-1 mB-3 p-2'>
                        <Text>{d._homeId}</Text>
                      </View>
                      <View className='mT-1 mB-3 p-2'>
                        <Text>{d.name}</Text>
                      </View>
                      <View className='mT-1 mB-3 p-2'>
                        <Text>{d.age + ', ' + (d.gender === 'M' ? 'Male' : (d.gender === 'F' ? 'Female' : 'Other'))}</Text>
                      </View>
                      {isNN(d.mobileNo) &&
                      <View className='mT-1 mB-3 p-2'>
                        <Text>{d.mobileNo}</Text>
                      </View>
                      }
                      {isNN(d.emailId) &&
                      <View className='mT-1 mB-3 p-2'>
                        <Text>{d.emailId}</Text>
                      </View>
                      }
                      {
                        d.lastClocking &&
                        <View className='mT-1 mB-3 p-2'>
                        {
                          (d.lastClocking.status === 'IN') ?
                          <Text className='c-red-600'>{'Entered At: ' + toDateTimeString(d.lastClocking.at)}</Text>
                          :
                          <Text>{'Exit At: ' + toDateTimeString(d.lastClocking.at)}</Text>
                        }
                        </View>
                      }
                      <View className='mT-1 mB-3 p-2'>
                      {
                        (d.lastClocking && d.lastClocking.status === 'IN') ?
                        <Text className='c-red-600'>Staying IN</Text>
                        :
                        <Text>Staying OUT</Text>
                      }
                      </View>
                      {(props.appUser.role === ROLE.GATEKEEPER) ?
                      <View className='mT-1 mB-3 p-2 ta-c'>
                      {
                        (d.lastClocking && d.lastClocking.status === 'IN') ?
                        <Button title='OUT' onPress={() => { onClocking(d._id, 'OUT') }} />
                        :
                        <Button title='IN' className='mR-10' onPress={() => { onClocking(d._id, 'IN') }} />
                      } 
                      </View>
                      :
                      <>
                      <View className='mT-1 mB-3 p-2'>
                        <span className='mR-15' onClick={() => {
                          onView(d._id, d.takeaways);
                        }}>
                          <Text className='cur-p c-blue-500'>{(viewId === d._id ? 'Hide' : 'View') + ' Takeaways'}</Text>
                        </span>
                        <span onClick={() => {
                          onViewClocking(d._id);
                        }}>
                          <Text className='cur-p c-blue-500'>{(clocking.viewId === d._id ? 'Hide' : 'View') + ' Clocking'}</Text>
                        </span>
                      </View>
                      {viewId === d._id &&
                        <View className='mT-5 mB-5'>
                          <View>
                            <Text className='fw-600'>Takeaways</Text>
                          </View>
                          <View className='p-15'>
                            {isNN(takeaways) ?
                              takeaways.map((e: ITakeaway, j: number) => {
                                return(
                                <View key={'tkycmt' + j} className={'pL-3 pR-3 pT-5 pB-5' + (j === 0 ? '' : ' bdT')}>
                                  <View>
                                    <Text className='fw-600'>{e.createdBy + ' on ' + toDateTimeString(e.updatedAt)}</Text>
                                  </View>
                                  <View>
                                    <Text>{e.title}</Text>
                                  </View>
                                  <View>
                                    <Text>{e.description}</Text>
                                  </View>
                                  {
                                    e.isExit ?
                                  <View>
                                    <Text className='c-green-600'>{'Exit by ' + e.exitBy + ' in Gate ' + e.exitGateNo + ' on ' + toDateTimeString(e.exitAt)}</Text>
                                  </View>
                                  :
                                  <View>
                                    <Text className='c-red-600'>Still not exit.</Text>
                                  </View>
                                  }
                                </View>
                                )
                              })
                              :
                              <Text className='p-20'>No takeaway found.</Text>
                            }
                          </View>
                        </View>
                        }
                        {clocking.viewId === d._id &&
                        <View className='mT-5 mB-5'>
                          <View>
                            <Text className='fw-600'>Clockings</Text>
                          </View>
                          <View className='p-15'>
                            {isNN(clocking.clockings) ?
                              clocking.clockings.map((e: any, j: number) => {
                                return(
                                <View key={'clkcmt' + j} className={'pL-3 pR-3 pT-5 pB-5' + (j === 0 ? '' : ' bdT')}>
                                  <View>
                                    <Text className='mR-15 fw-600'>{e.status}</Text><Text>{toDateTimeString(e.createdAt)}</Text>
                                  </View>
                                </View>
                                )
                              })
                              :
                              <Text className='p-20'>No clocking found.</Text>
                            }
                            {(isNN(clocking.clockings) && clocking.clockings.length % 10 === 0) &&
                            <span onClick={() => {
                              onViewMoreClockings(d._id);
                            }}>
                              <Text className='cur-p c-blue-500'>View More</Text>
                            </span>
                            }
                          </View>
                        </View>
                        }
                      </>
                      }
                    </View>
                  </View>
                )
              })
              :
              <Text className='p-20'>No servants found.</Text>
            }
            </View>

            <Spinner visible={isLoading} />
          </View>
        </View>
      </View>
    </View>
  );
};