import React, {useState, useEffect} from 'react';
import { View, TextInput, Image } from '../common/elements';
import Button from '../common/button';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { IUser } from '../../models/user/user';
import { isNN, onEnter, generateUUID } from '../../util/index';
import Toast from  '../../util/toast';
import { FILE_BASE_PATH } from '../../constants';
import Images from '../images';

interface IProps {
  user: IUser,
  onUpload: (data: any, callback: (result: boolean, message: string) => void, user: IUser) => void,
  onUpdate: (userId: string, name: string, mobileNo: string, emailId: string, image: string, user: IUser, callback: (result: boolean, message: string) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [name, setName] = useState('');
  const [mobileNo, setMobileNo] = useState('');
  const [emailId, setEmailId] = useState('');

  useEffect(() => {
    setName(props.user.name);
    setMobileNo(props.user.mobileNo);
    setEmailId(props.user.emailId);
  }, []);
  
  const onUpdate = () => {
    if(isNN(name) && isNN(mobileNo) && isNN(emailId)){
      setIsLoading(true);
      props.onUpdate(props.user._id, name, mobileNo, (emailId ? emailId.toLowerCase() : ''), ((props.user && props.user.image) ? props.user.image : ''), props.user, (result: boolean, message: string) => {
        setIsLoading(false);
        if(result === true){
          Toast.success('Profile updated successfully.');
        }else{
          Toast.fail(message);
        }
      });
    }else{
      Toast.warn('Please fill all the fields before Update.');
    }
  };
  
  const onImageSelect = (event: any) => {
    let file = event.target.files[0];
    if(file && file.name){
      let arrFileName = file.name.split('.');
      if(arrFileName && arrFileName.length > 1){
        let fileName: string = generateUUID();
        fileName += '.' + arrFileName[arrFileName.length - 1];
        let data = {
          file: file,
          type: file.type,
          name: fileName,
          isProfile: true
        };
        props.onUpload(data, (result: boolean, message: string) => {
          if(!result){
            Toast.fail(message ? message : 'Failed to upload profile image.');
          }else{
            onUpdate();
          }
        }, props.user);
      }
    }
  };

      return(
        <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
          <Breadcrum data={[{title: 'My Profile', link: 'my-profile'}]} />
          <View className='bgc-white p-20'>
            <View className='mT-3 mB-3 pT-5 pB-5 ta-c'>
              <Image src={isNN(props.user.image) ? FILE_BASE_PATH + props.user.image : (props.user.gender.toLocaleUpperCase() === 'F' ? Images.avatarFemale : Images.avatarMale)} className='m-0' style={{height: '25vh', borderRadius: '50%'}} />
              <input type='file' accept='image/*' name='profile-image' placeholder='SELECT IMAGE' onChange={onImageSelect}/>              
            </View>
            <View className='mT-3 mB-3 pT-5 pB-5'>
              <TextInput type='text' placeholder="Name" className='form-control' value={name} onChange={(event: any) => setName(event.target.value)} />
            </View>
            <View className='mT-3 mB-3 pT-5 pB-5'>
              <TextInput type='text' placeholder="Mobile Number" className='form-control' value={mobileNo} onChange={(event: any) => setMobileNo(event.target.value)} />
            </View>
            <View className='mT-3 mB-3 pT-5 pB-5'>
              <TextInput type='text' placeholder="Email Id" className='form-control' value={emailId} onChange={(event: any) => setEmailId(event.target.value)}
              onKeyPress={(e) => {
                onEnter(e, onUpdate);
              }} />
            </View>
            <View className='ta-c mT-3 mB-3 pT-5 pB-5'>
              <Button
              title="UPDATE"
              onPress={onUpdate}
              />
            </View>


          </View>
          <Spinner visible={isLoading} />
        </View>
        </View>
        </View>
      );
};