import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import { View, Image } from '../common/elements';
import BlockSpinner from '../common/block-spinner';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user';
import { IVisitor } from '../../models/home';
import { ROLE, FILE_BASE_PATH } from '../../constants';
import { isNN } from '../../util';

interface IProps {
  appUser: IAppUser,
  user: IUser,
  onLoad: (appClientId: string, homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(true);
  const [data, setData] = useState([] as Array<IVisitor>);

  useEffect(() => {
    setIsLoading(true);
    props.onLoad(props.user.client._id, (props.appUser.role === ROLE.USER ? props.user.homes[props.appUser.homeIndex]._id : ''), true, (result: Array<IVisitor>) => {
      setIsLoading(false);
      setData((result && result.length > 0) ? result : []);
    }, (message: string) => {
      setIsLoading(false);
    });
  }, []);

  return (
    <View className="masonry-item col-md-6">
    <View className="bd bgc-white">
      <View className="layers">
        <View className="layer w-100 p-20">
          <h6 className="lh-1">Active Visitors</h6>
        </View>
        {isLoading ?
          <BlockSpinner visible={true} />
          :
        <View className="layer w-100">
          <View className="bgc-light-blue-500 c-white p-20">
            <View className="peers ai-c jc-sb gap-40">
              <View className="peer peer-greed">
                <h5>Active Visitors</h5>
                <p className="mB-0">{props.user.homes[props.appUser.homeIndex].number}</p>
              </View>
              <View className="peer">
                <h3 className="text-right">{data.length}</h3>
              </View>
            </View>
          </View>
          <View className="table-responsive p-20">
            {isNN(data) ?
              <table className="table">
              <thead>
                <tr>
                  <th className=" bdwT-0">Photo</th>
                  <th className=" bdwT-0">Name</th>
                  <th className=" bdwT-0">Type</th>
                  <th className=" bdwT-0">Status</th>
                </tr>
              </thead>
              <tbody>
                {
                  data.map((d: IVisitor, i: number) => {
                    return(
                      <tr>
                        <td className='va-m'>
                          <Image src={FILE_BASE_PATH + d.image} className='m-0' style={{height: '7vh', borderRadius: '50%'}} />
                        </td>
                        <td className='fw-600 va-m'>{d.name}</td>
                        <td className='va-m'>{d.type}</td>
                        <td className='va-m'>
                          {(!d.isApproved && !d.isRejected && !d.isEntered) && 
                          <span className="badge bgc-blue-50 c-blue-500 p-10 lh-0 tt-c badge-pill">WAITING FOR APPROVAL</span>
                          }
                          {(d.isApproved || d.isRejected ) && 
                          <span className={"badge p-10 lh-0 tt-c badge-pill" + (d.isApproved ? 'bgc-green-50 c-green-600' : 'bgc-red-50 c-red-600')}>{d.isApproved ? 'APPROVED' : 'REJECTED'}</span>
                          }
                          {d.isEntered &&
                          <span className="badge bgc-deep-purple-50 c-deep-purple-700 p-10 lh-0 tt-c badge-pill">ENTERED</span>
                          }
                          {(d.isApproved && !d.isEntered) &&
                          <span className="badge bgc-red-50 c-red-600 p-10 lh-0 tt-c badge-pill">NOT ENTERED</span>
                          }
                          {(!d.isRejected && d.isExit) &&
                          <span className="badge bgc-orange-50 c-orange-700 p-10 lh-0 tt-c badge-pill">EXIT</span>
                          }
                          {(d.isEntered && !d.isExit) &&
                          <span className="badge bgc-red-50 c-red-700 p-10 lh-0 tt-c badge-pill">STAYING IN</span>
                          }
                        </td>
                      </tr>
                    )
                  })
                }
              </tbody>
            </table>
             :
             <View className="peers ai-c">
           <label className=" peers peer-greed js-sb ai-c">
             <span className="peer peer-greed">No visitors found !!!</span>
           </label>
         </View>
            }
          </View>
        </View>
        }
      </View>
      {isNN(data) &&
      <View className="ta-c bdT w-100 p-20">
        <Link to={props.appUser.role === ROLE.USER ? '/visitors' : '/all-visitors'}>View all visitors</Link>
      </View>
      }
    </View>
  </View>
  );
};