import React, {useState} from 'react';
import { View } from '../common/elements';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { IHome } from '../../models/home/home';
import { ITransaction } from '../../models/home/transaction';
import { IAds } from '../../models/store/ads';
import { ITickets } from '../../models/store/tickets';
import { IVisitor } from '../../models/home';
import MyDues from './my-dues';
import History from './history';
import CustomList from './custom-list';
import Banner from '../common/banner';
import Ads from '../common/ads';
import Tickets from '../common/tickets';
import Visitors from '../common/visitors';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  home: IHome,
  ads: IAds,
  tickets: ITickets,
  onPaymentUpload: (file: any, homeId: string, callback: (result: boolean) => void) => void,
  onPaymentUpdate: (paymentId: string, transaction: ITransaction, callback: (result: boolean) => void) => void,
  onLoadVisitors: (appClientId: string, homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void
};

export default (props: IProps) => {
  const [activeTab, setActiveTab] = useState(1);
  
    return(
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          {/* <View className='fw-600'>Dashboard</View> */}
          <View className="masonry-item w-100">
            <View className="row gap-20">
              <Banner title='Member(s)' icon='fa fa-users' color='c-brown-400' bgColor='bgc-brown-50' hasCount={true} count={props.home.members ? props.home.members.length + '' : '0'} onClick={() => props.history.push('members')} />
              <Banner title='Servant(s)' icon='fa fa-female' color='c-deep-purple-400' onClick={() => props.history.push('servants')} />
              <Banner title='Vehicle(s)' icon='fa fa-car' color='c-blue-600' bgColor='bgc-blue-50' hasCount={true} count={props.home.vehicles ? props.home.vehicles.length + '' : '0'} onClick={() => props.history.push('vehicles')} />
              <Banner title='Ticket(s)' icon='ti-headphone-alt' color='c-orange-400' bgColor='bgc-orange-50' hasCount={true} count={props.tickets.isUpToDate ? (!props.tickets.isError ? props.tickets.data.length + '' : '0') :'Loading'} onClick={() => props.history.push('tickets')} />
            </View>
            <View className="row gap-20">
              <Banner title='Visitor(s)' icon='fa fa-id-card-o' color='c-pink-400' onClick={() => props.history.push('visitors')} />
              <Banner title='Door Lock(s)' icon='ti-lock' color='c-indigo-700' onClick={() => props.history.push('outings')} />
            </View>
          </View>
          {props.user.client.config.hasPayment &&
          <View className="masonry-item col-12">
            <View className='tab-bar bgc-white p-20'>
              <span className={'tab' + (activeTab === 1 ? ' active': '')} onClick={() => setActiveTab(1)}>
                MY DUES
                </span>
              <span className={'tab' + (activeTab === 2 ? ' active': '')} onClick={() => setActiveTab(2)}>
                ADVANCE
              </span>
              <span className={'tab' + (activeTab === 3 ? ' active' : '')} onClick={() => setActiveTab(3)}>
                HISTORY
              </span>
              {/* <span className={'tab' + (activeTab === 4 ? ' active' : ')} onClick={() => setActiveTab(4)}>
                DEPOSITS
              </span> */}
            </View>
            <View className='bgc-white p-20'>
            {activeTab === 1 ?
                <MyDues history={props.history} user={props.user} home={props.home} onPaymentUpload={props.onPaymentUpload} onPaymentUpdate={props.onPaymentUpdate} />
              :
              activeTab === 2 ?
                <CustomList data={[]} message='No advance found!' />
                :
                activeTab === 3 ?
                <History history={props.history} home={props.home} />
                :
                activeTab === 4 ?
                <CustomList data={[]} message="Work in progress" />
                :
                []
              }
            </View>
          </View>
          }
          <Tickets data={props.tickets} role={props.appUser.role} />
          <Ads data={props.ads} />
          <Visitors appUser={props.appUser} user={props.user} onLoad={props.onLoadVisitors} />
        </View>
      </View>
    );
};