import * as React from 'react';
import { View } from '../common/elements';

interface IProps {
  visible: boolean,
  text?: string
};

export default (props: IProps) => {
  return (
    props.visible ?
    <View id='loader'>
      <View className="bg"></View>
      <View className="spinner"></View>
    </View>
    :
    null
  );
};