import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IServant } from '../models/home';
import { loadAll, clocking, loadClockings } from '../store/actions/servant';
import Page from '../components/home/gatekeeper/servants';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    appUser: state.AppUser,
    gateNo: state.GateNo
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (sCallback: (result: Array<IServant>) => void, fCallback: (message: string) => void) =>  dispatch(loadAll(sCallback, fCallback)),
    onClocking: (id: string, status: string, gateNo: string, callback: (result: string) => void) => dispatch(clocking(id, status, gateNo, callback)),
    onLoadClockings: (id: string, pn: number, rc: number, callback: (result: Array<any>) => void) => dispatch(loadClockings(id, pn, rc, callback))
});

export const AllServants = connect(mapStateToProps, mapDispatchToProps)(Page);