import * as React from 'react';
import { View } from '../../common/elements';
import { ROLE } from '../../../constants';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user';
import { IUsers } from '../../../models/store/users';
import { IHomes } from '../../../models/store/homes';
import { IBlocks } from '../../../models/store/blocks';
import { IAds } from '../../../models/store/ads';
import { ITickets } from '../../../models/store/tickets';
import { IVisitor } from '../../../models/home';
import Banner from '../../common/banner';
import Ads from '../../common/ads';
import Tickets from '../../common/tickets';
import Visitors from '../../common/visitors';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  users: IUsers,
  homes: IHomes,
  blocks: IBlocks,
  ads: IAds,
  tickets: ITickets,
  onLoadVisitors: (appClientId: string, homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void
};

export default (props: IProps) => {
  return(
    <View id='mainContent'>
      <View className="row gap-20 masonry pos-r">
        {/* <View className='fw-600'>Dashboard</View> */}
        {(props.appUser.role === ROLE.ADMIN || props.appUser.role === ROLE.MANAGER) &&
        <View className="masonry-item w-100">
          <View className="row gap-20">
            <Banner title='Ticket(s)' icon='ti-headphone-alt' color='c-orange-400' bgColor='bgc-orange-50' hasCount={true} count={props.tickets.isUpToDate ? (!props.tickets.isError ? props.tickets.data.length + '' : '0') :'Loading'} onClick={() => props.history.push('tickets')} />
            <Banner title='Visitor(s)' icon='fa fa-id-card-o' color='c-pink-400' onClick={() => props.history.push('all-visitors')} />
            <Banner title='ads' icon='fa fa-bullhorn' color='c-teal-400' bgColor='bgc-teal-50' hasCount={true} count={props.ads.isUpToDate ? (!props.ads.isError ? props.ads.data.length + '' : '0') :'Loading'} onClick={() => props.history.push('ads')} />
            <Banner title='Notification' icon='ti-bell' color='c-red-400' onClick={() => props.history.push('notification')} />
          </View>
          <View className="row gap-20">
            <Banner title='Door Lock(s)' icon='ti-lock' color='c-indigo-700' onClick={() => props.history.push('outings')} />
            {props.appUser.role === ROLE.ADMIN &&
            <>
              <Banner title='Block(s)' icon='fa fa-building' color='c-brown-400' bgColor='bgc-brown-50' hasCount={true} count={props.blocks.isUpToDate ? (!props.blocks.isError ? props.blocks.data.length + '' : '0') :'Loading'} onClick={() => props.history.push('blocks')} />
              <Banner title='House(s)' icon='fa fa-home' color='c-blue-600' bgColor='bgc-blue-50' hasCount={true} count={props.homes.isUpToDate ? (!props.homes.isError ? props.homes.data.length + '' : '0') :'Loading'} onClick={() => props.history.push('homes')} />
              <Banner title='User(s)' icon='fa fa-users' color='c-deep-purple-400' bgColor='bgc-deep-purple-50' hasCount={true} count={props.users.isUpToDate ? (!props.users.isError ? props.users.data.length + '' : '0') :'Loading'} onClick={() => props.history.push('users')} />
            </>
            }
            {(props.appUser.role !== ROLE.ADMIN && props.user.client.config.hasPayment) &&
            <Banner title='Payment' icon='fa fa-credit-card' color='c-indigo-400' onClick={() => props.history.push('payment')} />
            }
          </View>
          {(props.appUser.role === ROLE.ADMIN && props.user.client.config.hasPayment) &&
          <View className="row gap-20">
            <Banner title='Payment' icon='fa fa-credit-card' color='c-indigo-400' onClick={() => props.history.push('payment')} />
          </View>
          }
        </View>
        }
        {(props.appUser.role === ROLE.MAINTENANCE_SUPERVISOR || props.appUser.role === ROLE.MAINTENANCE_MANAGER) &&
        <View className="masonry-item w-100">
          <View className="row gap-20">
            <Banner title='Ticket(s)' icon='ti-headphone-alt' color='c-orange-400' onClick={() => props.history.push('tickets')} />
          </View>
        </View>
        }
        {(props.appUser.role === ROLE.SECURITY_SUPERVISOR || props.appUser.role === ROLE.SECURITY_MANAGER) &&
        <View className="masonry-item w-100">
          <View className="row gap-20">
            <Banner title='Visitor(s)' icon='fa fa-id-card-o' color='c-pink-400' onClick={() => props.history.push('all-visitors')} />
            <Banner title='Door Lock(s)' icon='ti-lock' color='c-indigo-700' onClick={() => props.history.push('outings')} />
          </View>
        </View>
        }
        {(props.appUser.role === ROLE.ADMIN || props.appUser.role === ROLE.MANAGER || props.appUser.role === ROLE.MAINTENANCE_SUPERVISOR || props.appUser.role === ROLE.MAINTENANCE_MANAGER) &&
        <Tickets data={props.tickets} role={props.appUser.role} />
        }
        <Ads data={props.ads} />
        {(props.appUser.role === ROLE.ADMIN || props.appUser.role === ROLE.MANAGER || props.appUser.role === ROLE.SECURITY_SUPERVISOR || props.appUser.role === ROLE.SECURITY_MANAGER) &&
        <Visitors appUser={props.appUser} user={props.user} onLoad={props.onLoadVisitors} />
        }
      </View>
    </View>
  );
};