import { actionTypes } from '../action-types';
import Api from '../api/api';
import { isNNObject } from '../../util/index';
import { IUser } from '../../models/user';

let controller: string = 'upload';

export const upload = (data: any, callback: (result: boolean, message: string) => void, user?: IUser) => (dispatch: any) => {
  new Api().upload(controller, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true)
    {
      if(data && data.isProfile && data.isProfile == true && user){
        user.image = data.name;
        dispatch(getUserResult(user));
      }
      callback(true, '');
    }else{
      callback(false, message);
    }
  }, data);
};

const getUserResult = (data: IUser) => ({
  type: actionTypes.LOAD_USER,
  payload: data
});